.Artboard {
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.Artboard .canvas {
  position: absolute;
  margin: 0;
  top: 50%;
  left: 50%;
  background-image: url("transparent.svg");
  background-repeat: repeat;
  box-shadow: 0 0 5px black;
  z-index: 0;
}

.Artboard canvas {
  position: absolute;
  top: 0;
  left: 0;
  image-rendering: pixelated;
}

.Artboard #drawing {
  z-index: 1;
}
