.palette {
  --size-color: 32px;
  --num-column: 8;
  --num-row: 2;
  width: calc(var(--size-color) * var(--num-column));
  height: calc(var(--size-color) * var(--num-row));
  margin: 8px auto;
  user-select: none;
}

.palette div.color {
  position: relative;
  float: left;
  width: var(--size-color);
  height: var(--size-color);
}

.palette div.color.selected {
  width: calc(var(--size-color) - 4px);
  height: calc(var(--size-color) - 4px);
  border: 2px solid white;
}

.palette div.color.selected>div {
  position: absolute;
  top: -2px;
  left: -2px;
  width: calc(var(--size-color) - 2px);
  height: calc(var(--size-color) - 2px);
  border: 1px solid black;
}