.Toolbox {
  position: relative;
  bottom: 0;
  width: 32px;
  height: 32px;
}

.Toolbox .anchor {
  position: absolute;
  bottom: 0;
  width: 32px;
  height: 32px;
}

.Toolbox .eraser {
  background-image: url("eraser.png");
  background-size: contain;
}

.Toolbox .selection {
  background-image: url("selection.png");
  background-size: contain;
}

.Toolbox .tools {
  position: absolute;
  bottom: 32px;
}

.Toolbox .hidden {
  display: none;
}
