.App {
  font-family: sans-serif;
  text-align: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background: var(--color-bg);
  color: white;
}

.App button {
  color: var(--color-btn);
  font-size: 14px;
  font-weight: 600;
  background: none;
  outline: none;
  border: none;
  z-index: 100;
}

.App button:active,  /* button pressed */
.App button[disabled],
.Add button:disabled {
  color: var(--color-btn-inactive);
}

.App .top-left {
  position: fixed;
  top: var(--margin-y);
  left: var(--margin-x);
}

.App .top-right {
  position: fixed;
  top: var(--margin-y);
  right: var(--margin-x);
}

.App .bottom-left {
  position: fixed;
  bottom: var(--margin-bottom);
  left: var(--margin-x);
  display: grid;
  grid-template-columns: 1 1 1;
  grid-template-rows: 1;
}

.App .bottom-left > .Toolbox {
  grid-row: 1;
  width: 40px;
}

.App .bottom-left > button {
  grid-row: 1;
  width: 32px;
}

.App .bottom-right {
  position: fixed;
  bottom: var(--margin-bottom);
  right: var(--margin-x);
}
